:root {
  --font-be-vietnam-pro: "Be Vietnam Pro", sans-serif;
}

:root {
  --red: hsl(12, 88%, 59%);
  --faded-red: hsl(12, 88%, 69%);
  --blue: hsl(228, 39%, 23%);
  --grayish-blue: hsl(227, 12%, 61%);
  --dark-blue: hsl(233, 12%, 13%);
  --pale-red: hsl(13, 100%, 96%);
  --light-gray: hsl(0, 0%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-be-vietnam-pro);
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 64em) {
  .no-scroll {
    overflow: auto;
  }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

button {
  cursor: pointer;
}

h1,
h2,
h3 {
  color: var(--blue);
  line-height: 1.1;
  margin: 0;
}

h1 {
  font-size: clamp(2.2rem, 5vw, 3rem);
}

h2 {
  font-size: clamp(2rem, 5vw, 2.2rem);
  line-height: 1.5;
}

p {
  line-height: 1.7;
  color: var(--grayish-blue);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

@media (min-width: 64em) {
  h2 {
    line-height: 1.1;
  }
}
.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
.container--desktop-only {
  max-width: 69.375rem;
  margin: 0 auto;
}

.header {
  position: relative;
  padding: 2rem 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav--active .nav__list {
  display: block;
}
.nav--active .nav__overlay {
  display: block;
}
.nav--active .nav__toggle-hamburger {
  display: none;
}
.nav--active .nav__toggle-close {
  display: block;
}
.nav__list {
  display: none;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 3rem);
  text-align: center;
  background-color: var(--light-gray);
  border-radius: 0.25rem;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
  padding: 2rem 0;
}
.nav__list-item {
  margin-bottom: 0.25rem;
}
.nav__link {
  display: block;
  font-weight: 700;
  color: var(--blue);
  padding: 1rem;
}
.nav__overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  pointer-events: none;
}
.nav__toggle {
  background-color: transparent;
  border: 0;
}
.nav__toggle-close {
  display: none;
}
@media (min-width: 64em) {
  .nav--active .nav__list {
    display: flex;
  }
  .nav--active .nav__overlay {
    display: none;
  }
  .nav__overlay {
    display: none;
  }
  .nav__list {
    background-color: transparent;
    box-shadow: none;
    position: static;
    transform: translateX(0);
    width: auto;
    display: flex;
    gap: 2rem;
    padding: 0;
  }
  .nav__list-item {
    margin-bottom: 0;
  }
  .nav__link {
    padding: 0;
  }
  .nav__link:hover {
    color: var(--grayish-blue);
  }
  .nav__toggle {
    display: none;
  }
}

.hero {
  text-align: center;
  padding: 2rem 0;
}
.hero__content {
  max-width: 40rem;
  margin: 0 auto;
}
.hero__body {
  margin-bottom: 1.5rem;
}
.hero__bg-img {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(35%, -26%);
  z-index: -1;
}
@media (min-width: 64em) {
  .hero {
    text-align: left;
    padding: 3rem 0;
  }
  .hero__inner {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
  .hero__body {
    margin-bottom: 2.5rem;
  }
}

.features {
  padding: 5rem 0;
}
.features__heading {
  text-align: center;
  padding: 0 1.5rem;
  max-width: 40rem;
  margin: 0 auto 3rem;
}
.features__list {
  padding-left: 1rem;
}
.features__list-heading {
  font-size: 1rem;
  background: var(--pale-red);
  padding: 0.75rem 0;
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
.features__list-marker {
  background: var(--red);
  color: var(--light-gray);
  padding: 0.75rem 1.75rem;
  border-radius: 2rem;
  margin-right: 0.5rem;
}
.features__list-body {
  max-width: 56.25rem;
  padding-right: 1rem;
}
@media (min-width: 64em) {
  .features {
    text-align: left;
  }
  .features__inner {
    display: flex;
    justify-content: space-between;
  }
  .features__heading {
    text-align: left;
    flex-basis: 45%;
  }
  .features__list-wrapper {
    flex-basis: 50%;
  }
  .features__list-heading {
    background: transparent;
  }
  .features__list-marker {
    margin-right: 2rem;
  }
  .features__list-body {
    margin-left: 6.9rem;
  }
}

.testimonials {
  text-align: center;
  position: relative;
  padding: 5rem 0;
}
.testimonials__header {
  margin-bottom: 5rem;
}
.testimonials__bg-img {
  display: none;
}
@media (min-width: 64em) {
  .testimonials__bg-img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    z-index: -1;
  }
}

.cta {
  background-color: var(--red);
  background-image: url("../../../images/bg-simplify-section-mobile.svg");
  background-repeat: no-repeat;
  background-position: left center;
  text-align: center;
  padding: 5rem 0;
}
.cta__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.cta__header {
  color: var(--light-gray);
}
@media (min-width: 64em) {
  .cta {
    background-image: url("../../../images/bg-simplify-section-desktop.svg");
    background-position: center;
    text-align: left;
  }
  .cta__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .cta__header {
    flex-basis: 50%;
  }
}

.footer {
  background-color: var(--dark-blue);
  padding: 3rem 0;
}
.footer__inner {
  display: grid;
  justify-content: center;
  justify-items: center;
  grid-template-columns: 1fr;
  gap: 3rem;
}
.footer__form {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
  width: 100%;
}
.footer__email-wrapper {
  flex: 1;
}
.footer__email {
  font: inherit;
  border-radius: 5rem;
  border: none;
  width: 100%;
  padding: 1rem 1.5rem;
}
.footer__form--invalid .footer__email {
  border: 1px solid var(--red);
  color: var(--red);
}
.footer__form-error {
  font-size: 0.875rem;
  display: none;
  color: var(--red);
  padding: 0.5rem 1.75rem;
}
.footer__form--invalid .footer__form-error {
  display: block;
}
.footer__nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.footer__nav-list-item {
  margin-bottom: 0.25rem;
}
.footer__nav-link {
  display: inline-block;
  color: var(--light-gray);
  padding: 0.25rem;
}
.footer__socials-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
.footer__socials {
  display: flex;
  gap: 1rem;
}
.footer__copyright {
  color: var(--grayish-blue);
}
@media (min-width: 64em) {
  .footer__inner {
    grid-template-columns: repeat(3, 1fr);
    gap: 0;
  }
  .footer__form {
    grid-column: 3/4;
  }
  .footer__nav {
    grid-column: 2/3;
    grid-row: 1/4;
  }
  .footer__nav-link:hover {
    color: var(--red);
  }
  .footer__socials-wrapper {
    grid-column: 1/2;
    grid-row: 1/4;
    flex-direction: column-reverse;
    justify-content: space-between;
  }
  .footer__social:hover .footer__social-icon {
    fill: var(--red);
  }
  .footer__copyright {
    grid-column: 3/4;
    grid-row: 3/4;
    justify-self: end;
  }
}

.glide {
  overflow: hidden;
  padding: 5rem 0;
  margin-bottom: 3rem;
}
.glide__track {
  margin-bottom: 3rem;
}
.glide__slides {
  display: flex;
}
.glide__slide-inner {
  background-color: var(--light-gray);
  text-align: center;
  position: relative;
  padding: 7rem 2rem 1.5rem;
}
.glide__slide-img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
.glide__bullets {
  display: flex;
  justify-content: center;
  gap: 0.25rem;
}
.glide__bullet {
  background-color: var(--light-gray);
  border: 2px solid var(--red);
  border-radius: 50%;
  padding: 0.25rem;
}
.glide__bullet--active {
  background-color: var(--red);
}

@media (min-width: 64em) {
  .glide {
    margin-bottom: 0;
  }
  .glide__bullet {
    display: none;
  }
}
.btn {
  font-weight: 500;
  background-color: var(--red);
  color: var(--pale-red);
  display: inline-block;
  border-radius: 5rem;
  box-shadow: 0px 10px 10px rgba(242, 95, 58, 0.4);
  padding: 1rem 2.5rem;
}
.btn:hover {
  background-color: var(--faded-red);
}
.btn--white {
  background-color: var(--light-gray);
  color: var(--red);
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
}
.btn--white:hover {
  background-color: var(--light-gray);
  color: var(--faded-red);
}
.btn--small {
  font: inherit;
  font-weight: 700;
  text-transform: uppercase;
  border: none;
  box-shadow: none;
  padding: 1rem 2rem;
}
.btn--hidden {
  display: none;
}
@media (min-width: 64em) {
  .btn--small {
    text-transform: none;
  }
  .btn--hidden {
    display: inline-block;
  }
}